.selection {
  background-image: url("../../img/background-cat-mobile.jpg");
  background-repeat: no-repeat;
  background-size: 320px auto;
  padding-bottom: 20px;
  background-position: 100% 0;
  max-width: 320px;
  position: relative;
  z-index: 5;

  @media (min-width: $tablet-width) {
    background-image: none;
    padding-bottom: 45px;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../../img/background-cat-desktop.jpg");
    background-repeat: no-repeat;
    background-position: 100% 0;
    background-size: 720px 694px;
    padding-bottom: 0;
    max-width: 1440px;
  }
}

@media (min-resolution: 2dppx) {
  .selection {
    background-image: url("../../img/background-cat-mobile@2x.jpg");
  }
}

@media (min-width: $desktop-width) {
  .selection {
    @media (min-resolution: 2dppx) {
      background-image: url("../../img/background-cat-desktop@2x.jpg");
    }
  }
}

.selection::before {
  content: "";
  width: 320px;
  height: 302px;
  display: block;
  position: absolute;
  background-color: rgba(104, 183, 56, 0.85);
  z-index: -1;
}

@media (min-width: $tablet-width) {
  .selection::before {
    background-color: #ffffff;
  }
}

@media (min-width: $desktop-width) {
  .selection::before {
    content: "";
    width: 720px;
    height: 694px;
    display: block;
    position: absolute;
    background-color: rgba(104, 183, 56, 0.85);
    right: 0;
  }
}

.selection__wrapper {
  padding-left: 20px;
  padding-right: 20px;

  @media (min-width: $tablet-width) {
    position: relative;
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (min-width: $desktop-width) {
  .selection__wrapper {
    padding-left: 110px;
    padding-right: 110px;
  }
}

.selection__description {
  color: $basicwhite;
  text-align: center;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;
  margin: 0;
  padding: 27px 0 25px 0;

  @media (min-width: $tablet-width) {
    color: #000000;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 60px;
    font-weight: 400;
    line-height: 60px;
    text-align: left;
    margin: 0;
    width: 550px;
    padding: 60px 0 40px 89px;
  }
}

@media (min-width: $desktop-width) {
  .selection__description {
    padding: 225px 0 40px 26px;
    width: 550px;
  }
}

.selection__slogan {
  color: #ffffff;
  text-align: center;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 29px;
  padding: 0 27px 0 24px;

  @media (min-width: $tablet-width) {
    color: #000000;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
    margin: 0;
    text-align: left;
    text-transform: uppercase;
    padding-left: 90px;
    padding-right: 0;
    margin-bottom: 23px;
  }
}

@media (min-width: $desktop-width) {
  .selection__slogan {
    padding-left: 30px;
  }
}

.selection__main-button {
  display: block;
  color: #ffffff;
  text-align: center;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  background-color: #68b738;
  text-decoration: none;
  width: 280px;
  padding: 10px 44px;
  transition: background-color 0.4s;

  &:hover {
    background-color: #5eaa2f;
  }
}

@media (min-width: $tablet-width) {
  .selection__main-button {
    position: absolute;
    width: 244px;
    padding: 14px 26px;
    font-size: 20px;
    font-weight: 400;
    line-height: 26px;
    top: 290px;
  }
}

@media (min-width: $desktop-width) {
  .selection__main-button {
    position: static;
    margin-top: 28px;
  }
}

@media (min-width: $tablet-width) {
  .selection__main-button--wrapper {
    padding-left: 90px;
  }
}

@media (min-width: $desktop-width) {
  .selection__main-button--wrapper {
    padding-left: 30px;
  }
}

.selection__main-button:active {
  color: rgba(255, 255, 255, 0.3);
}

.selection__catalog {
  display: flex;
  flex-direction: column;
  padding-top: 20px;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    padding: 0;
    width: 708px;
  }
}

@media (min-width: $desktop-width) {
  .selection__catalog {
    display: flex;
    margin-top: 265px;
    width: 1220px;
  }
}

.selection__list {
  margin: 0;
  padding: 0;

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    gap: 0 80px;
    flex-wrap: wrap;
  }
}

.selection__item {
  background-color: $basicgrey;
  list-style-type: none;
  margin-bottom: 20px;
  padding-bottom: 22px;

  @media (min-width: $tablet-width) {
    padding-bottom: 57px;
    margin-bottom: 30px;
  }
}

.selection__title--slim::before {
  background-image: url("../../img/cat-black-slim.svg");
  background-repeat: no-repeat;
  width: 50px;
  height: 53px;
  position: absolute;
  content: "";
  left: 20px;
  bottom: -6px;

  @media (min-width: $tablet-width) {
    background-image: url("../../img/cat-catalog-slim.svg");
    background-repeat: no-repeat;
    width: 200px;
    height: 211px;
    position: absolute;
    content: "";
    left: unset;
    right: 63px;
    bottom: -154px;
  }
}

@media (min-width: $desktop-width) {
  .selection__item {
    width: 570px;
    padding-bottom: 55px;
    margin-bottom: 0;
  }
}

.selection__item:last-child {
  margin: 0;
}

.selection__title {
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 37px;
  text-transform: uppercase;
  margin: 0;
  padding: 30px 0 0 90px;
  position: relative;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 36px;
    padding: 77px 0 22px 53px;

    @media (min-width: $desktop-width) {
      text-align: center;
      padding: 77px 0 73px 0;
    }
  }
}

@media (min-width: $desktop-width) {
  .selection__title--slim::before {
    background-image: url("../../img/catblack-slim-desktop.svg");
    background-repeat: no-repeat;
    width: 100px;
    height: 106px;
    position: absolute;
    content: "";
    left: 52px;
    bottom: 38px;
  }
}

.selection__title--weight::before {
  background-image: url("../../img/cat-black-weight.svg");
  background-repeat: no-repeat;
  width: 67px;
  height: 50px;
  position: absolute;
  content: "";
  left: 13px;
  bottom: -8px;

  @media (min-width: $tablet-width) {
    background-image: url("../../img/cat-catalog-pro.svg");
    background-repeat: no-repeat;
    width: 268px;
    height: 200px;
    position: absolute;
    content: "";
    left: unset;
    bottom: -155px;
    right: 30px;
  }
}

@media (min-width: $desktop-width) {
  .selection__title--weight::before {
    background-image: url("../../img/catblack-pro-desktop.svg");
    background-repeat: no-repeat;
    width: 134px;
    height: 100px;
    position: absolute;
    content: "";
    left: 33px;
    bottom: 38px;
  }
}

@media (min-width: $desktop-width) {
  .selection__title--weight {
    padding-left: 70px;
  }
}

.selection__text {
  color: #444444;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  margin: 0;
  padding: 29px 33px 40px 20px;
  position: relative;

  @media (min-width: $tablet-width) {
    color: #000000;
    font-family: "Lato", "Arial", sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    margin: 0;
    padding: 0 316px 52px 52px;
    position: relative;
  }
}

@media (min-width: $desktop-width) {
  .selection__text {
    padding: 0 61px 28px 52px;
  }
}

.selection__text::before {
  position: absolute;
  content: "";
  bottom: 20px;
  width: 240px;
  height: 1px;
  background-color: #d9d9d9;

  @media (min-width: $tablet-width) {
    // width: calc(100% - 223px);
    position: static;
  }
}

.selection__link {
  display: block;
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  padding-left: 20px;
  position: relative;
  text-align: left;

  &:hover {
    color: #000000;
  }
}

.selection__link:active {
  opacity: 0.3;
}

.selection__link::after {
  background-image: url("../../img/icons/arrow-catalog-slim.svg");
  background-repeat: no-repeat;
  width: 32px;
  height: 12px;
  position: absolute;
  content: "";
  right: unset;
  top: 2px;
  left: 120px;

  @media (min-width: $tablet-width) {
    background-image: url("../../img/icons/arrow-catalog-slim.svg");
    background-repeat: no-repeat;
    width: 32px;
    height: 12px;
    position: absolute;
    content: "";
    right: 492px;
    top: 9px;
    left: unset;
  }
}

@media (min-width: $desktop-width) {
  .selection__link::after {
    right: 356px;
    top: 9px;
    left: unset;
  }
}

@media (min-width: $tablet-width) {
  .selection__link {
    color: #000000;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    text-transform: uppercase;
    padding-left: 53px;
    position: relative;
  }
}

@media (min-width: $tablet-width) {
  .selection__head-block {
    display: inline-block;
  }
}

@media (min-width: $desktop-width) {
  .selection__head-block {
    padding-left: 50px;
    display: flex;
    flex-direction: column;
  }
}

@media (min-width: $tablet-width) {
  .selection__picture {
    width: 709px;
    height: 609px;
  }
}

@media (min-width: $desktop-width) {
  .selection__picture {
    position: absolute;
    content: "";
    width: 552px;
    height: 499px;
    right: 330px;
    top: 145px;
  }
}
