.social__list {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
  justify-content: center;
  gap: 24px;
  flex-wrap: wrap;

  @media (min-width: $tablet-width) {
    margin-left: 35px;
  }
}

.social__link {
  color: #666666;
}

.social__link:hover {
  color: #68b738;
}

.social__link:focus {
  color: #68b738;
}

.social__link:active {
  color: #68b738;
  opacity: 0.3;
}
